  height: 100%;
  padding: 20px 25px 25px; }

.partner__title {
  margin: 0;
  margin-bottom: 17px;
  padding-right: 150px;
  font-weight: 600;
  font-size: 28px;
  line-height: 40px;
  text-transform: uppercase; }

.partner__form-variants {
  display: flex;
  justify-content: space-between;
  width: 450px;
  margin-bottom: 30px; }

.partner__form-variant {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 210px;
  cursor: pointer; }
  .partner__form-variant:hover,
  .partner__form-variant input:focus ~,
  .partner__form-variant input:checked ~ {
    outline: none; }
    .partner__form-variant:hover .partner__form-variant-title,
    .partner__form-variant input:focus ~ .partner__form-variant-title,
    .partner__form-variant input:checked ~ .partner__form-variant-title {
      color: #333333; }
    .partner__form-variant:hover .partner__form-variant-desc,
    .partner__form-variant input:focus ~ .partner__form-variant-desc,
    .partner__form-variant input:checked ~ .partner__form-variant-desc {
      font-weight: 700;
      color: #df3d93; }
      .partner__form-variant:hover .partner__form-variant-desc::before,
      .partner__form-variant input:focus ~ .partner__form-variant-desc::before,
      .partner__form-variant input:checked ~ .partner__form-variant-desc::before {
        width: 100%; }

.partner__form-variant-title {
  margin: 0;
  margin-bottom: 12px;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #707070;
  text-transform: uppercase;
  transition: color 0.3s; }

.partner__form-variant-desc {
  position: relative;
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #707070;
  text-transform: uppercase;
  transition: color 0.3s; }
  .partner__form-variant-desc::before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: #df3d93;
    border-radius: 1px;
    transition: width 0.3s; }

.partner__form-stage {
  display: grid;
  grid-template-rows: repeat(3, 48px);
  grid-template-columns: 235px repeat(2, 200px);
  gap: 20px;
  margin-bottom: 20px; }
  @media (min-width: 3840px) {
    .partner__form-stage {
      grid-template-columns: repeat(3, 1fr); } }

.partner__form-field {
  position: relative;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #707070; }
  .partner__form-field input {
    box-sizing: border-box;
    width: 100%;
    padding: 14px 10px;
    font: inherit;
    color: inherit;
    background: none;
    border: 1px solid currentColor;
    border-radius: 10px;
    transition: color 0.3s; }
    .partner__form-field input.filled, .partner__form-field input:hover, .partner__form-field input:focus {
      color: #333333; }
      .partner__form-field input.filled + span, .partner__form-field input:hover + span, .partner__form-field input:focus + span {
        color: #333333; }
    .partner__form-field input.filled, .partner__form-field input:focus {
      outline: none; }
      .partner__form-field input.filled + span, .partner__form-field input:focus + span {
        transform: translateY(-15px) scale(0.8); }
  .partner__form-field span {
    position: absolute;
    top: 14px;
    left: 10px;
    transform-origin: left;
    transition: color 0.3s, transform 0.3s;
    pointer-events: none;
    user-select: none; }

.partner__form-privacy {
  position: relative;
  display: inline-block;
  margin-bottom: 20px;
  padding-left: 32px;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #707070;
  transition: color 0.3s;
  cursor: pointer; }
  .partner__form-privacy input {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
    width: 18px;
    height: 18px;
    opacity: 0;
    cursor: pointer; }
    .partner__form-privacy input:checked + span::after {
      transform: none; }
  .partner__form-privacy span::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 18px;
    height: 18px;
    border: 1px solid #ff46a7;
    border-radius: 5px; }
  .partner__form-privacy span::after {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    width: 8px;
    height: 8px;
    background-color: #ff46a7;
    border-radius: 2px;
    transform: scale(0);
    transition: transform 0.3s; }
  .partner__form-privacy:hover, .partner__form-privacy:focus-within {
    color: #333333; }

.partner__form-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer {
  background-color: #333333;
  padding: 36px 0; }

.footer__top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 33px;
  border-bottom: 1px solid #707070; }

.footer__logo span {
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  color: #ffffff;
  width: 100px;
  margin: auto 33px; }

.footer__logo {
  display: flex;
  min-height: 60px; }

.footer__menu {
  margin: auto 0; }

.footer__menu-list {
  display: flex;
  flex-wrap: wrap; }

.footer__menu-item {
  margin: 0 30px 5px 30px; }
  .footer__menu-item:last-child {
    margin-right: 0; }

.footer__menu-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #e5e5e5;
  transition: color 0.3s; }
  .footer__menu-link::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 1px;
    background-color: #ff46a7;
    transition: color 0.3s, width 0.3s; }
  .footer__menu-link--active::before {
    width: 24px; }
  .footer__menu-link--active, .footer__menu-link:hover, .footer__menu-link:focus {
    color: #ff46a7;
    outline: none; }
  @media (min-width: 3840px) {
    .footer__menu-link {
      font-size: 21px;
      line-height: 1.2; } }

.footer__bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 24px; }

.footer__bottom-link,
.footer__bottom-copyright {
  color: #ffffff;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  margin: 0; }
  @media (min-width: 3840px) {
    .footer__bottom-link,
    .footer__bottom-copyright {
      font-size: 21px;
      line-height: 1.2; } }
